.weekNav{display:flex;justify-content:space-between;align-items:center;margin:5rem auto;padding:1rem;box-sizing:border-box;background-color:#D2BEB0;border-radius:0.5rem;width:30vw;font-family:"Calligraffitti",cursive}.weekNav__arrow{font-size:2.5rem;padding:0.5rem;color:#3A465C}.weekNav__arrow path{pointer-events:none}.weekNav__arrow--day{display:none}@media (max-width: 600px){.weekNav{width:100%;padding:0.5rem}.weekNav__arrow{width:1rem;font-size:1.8rem}.weekNav__arrow--day{display:inline-block}}

.weekInfo{flex:1 1;text-align:center;color:#3A465C}.weekInfo__mainTitle,.weekInfo__subtitle{margin:0.5rem auto}.weekInfo__mainTitle{font-size:2rem}.weekInfo__subtitle{font-size:1.3rem}.weekInfo__subtitle--day{display:none}@media (max-width: 600px){.weekInfo__mainTitle,.weekInfo__subtitle{margin:0}.weekInfo__subtitle--week{display:none}.weekInfo__subtitle--day{display:inline}}

.tasksList{width:60%;margin:1rem auto;text-align:center}.tasksList__title{font-family:"Calligraffitti",cursive;font-size:1.3rem;color:#3A465C;padding:0.5rem}.tasksList__day{width:4rem}.tasksList__day:nth-of-type(odd){background-color:#D2BEB0}@media (max-width: 600px){.tasksList{margin:1rem 0;width:100%}.tasksList__title--invisible{display:none}.tasksList__day:nth-of-type(odd){background-color:#fff}}

.loader{margin:0 auto;animation:spin 1s infinite linear;border:solid 2vmin transparent;border-radius:50%;border-right-color:#3A465C;border-top-color:#3A465C;box-sizing:border-box;height:20vmin;left:calc(50% - 10vmin);top:calc(50% - 10vmin);width:20vmin;z-index:1}.loader:before{animation:spin 2s infinite linear;border:solid 2vmin transparent;border-radius:50%;border-right-color:#BB9684;border-top-color:#BB9684;box-sizing:border-box;content:"";height:16vmin;left:0;position:absolute;top:0;width:16vmin}.loader:after{animation:spin 3s infinite linear;border:solid 2vmin transparent;border-radius:50%;border-right-color:#D2BEB0;border-top-color:#D2BEB0;box-sizing:border-box;content:"";height:12vmin;left:2vmin;position:absolute;top:2vmin;width:12vmin}@keyframes spin{100%{transform:rotate(360deg)}}

